body{
  background: #33d9b2;
  font-size: 19px;
}
body, button, input{
  font-family:  'Righteous', cursive;
  font-weight: 700;
  letter-spacing: 1.4px;
}
.background{
  display: flex;
  min-height: 100vh;
  width: 50%;
  margin-left: 30px;
  margin-top: 150px;
}
.container{
  flex:  0 1700px;
  padding: 10px;
}
.screen{
  position: relative;
  background: #3e3e3e;
  border-radius: 15px;
}
.screen:after{
  content: "";
  display: block;
  position:absolute;
  top: 0;
  left: 20px;
  right: 20px;
  bottom: 0;
  border-radius: 15px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, .4);
  z-index: -1;
}
.screen-body{
  display: flex;
}
.screen-body-item{
  flex: 1;
  padding: 50px;
}
.screen-body-item .left{
  display: flex;
  flex-direction:column ;
  justify-content: center;
 align-items: center; 

}
.app-title{
  display: flex;
  justify-content: center;
  align-items: center;
  color: #33d9b2;
  font-size: 26px;
  text-transform: uppercase;
  border-left: 4px solid #218c74;
  padding: 10px;

}
.app-form-group{
  margin-bottom: 15px;
}
.app-form-group.buttons{
  margin-bottom: 0;
  text-align: right;

}
.app-form-control{
  width: 100%;
padding: 10px 0;
background: none;
border: none;
border-bottom: 1px solid #666;
color: #ddd;
font-size: 14px;
outline : none;
transition: border-color .2s;
}
.app-form-control::placeholder{
  color: rgb(116, 109, 109);
}
.app-form-control:focus{
  border-bottom-color: #ddd;
}
.app-from-button{
 background: none;
border: none;
color: #33d9b2;
font-size: 15px;
cursor: pointer;
outline: none;
text-transform: uppercase;
}
.app-from-button:hover{
  color: #218c74;
}
